import React, {Component} from 'react';
import Breadcrumb from "../component/Breadcrumb";
import BreadcrumbItem from "../component/BreadcrumbItem";
import Icon from "../component/Icon";


class PageBreadcrumber extends Component {
    constructor(props){
        super(props);
        this.state={
            fSize:24,
            current:0
        }
    }
    render() {
        return (
            <div>

                <div className={"titleColor fSize"+this.state.fSize}> Breadcrumber  </div>
                <div className="fSize14 bodyColor">
                    <p></p>
                </div>
                <p>Basic Breadcrumber  </p>
                <Breadcrumb separator=">">
                    <BreadcrumbItem>Home</BreadcrumbItem>
                    <BreadcrumbItem><a href=""></a></BreadcrumbItem>
                    <BreadcrumbItem><a href=""></a></BreadcrumbItem>
                    <BreadcrumbItem><a href=""></a></BreadcrumbItem>
                </Breadcrumb>
                <br/>
                <br/>
                <br/>
                <p>Icon Breadcrumber  </p>
                <Breadcrumb>
                    <BreadcrumbItem href="/#/icon">
                        <Icon title="icon-home"/>

                    </BreadcrumbItem>
                    <BreadcrumbItem href="">
                        <Icon title="icon-folder-open"/>
                        <span></span>
                    </BreadcrumbItem>
                    <BreadcrumbItem href="">
                        <Icon title="icon-file-text"/>
                        <span></span>
                    </BreadcrumbItem>

                </Breadcrumb>
                <Breadcrumb>
                    <BreadcrumbItem>
                        <Icon title="icon-home"/>
                    </BreadcrumbItem>
                    <BreadcrumbItem>
                        <span></span>
                    </BreadcrumbItem>
                </Breadcrumb>
            </div>
        );
    }
}

export default PageBreadcrumber;