import React, {Component} from 'react';
import Switch from "../component/Switch";

class PageSwitch extends Component {
    constructor(props){
        super(props);
        this.state={
            fSize:24,
        }
    }
    render() {
        return (
            <div>
                <div className={"titleColor fSize"+this.state.fSize}>Switch  </div>
                <div className="fSize14 bodyColor">
                    <p>/</p>
                </div>
                <Switch defaultChecked disabled></Switch>
                <p>Switch </p>
                <br/><br/><br/><br/>
                <Switch disabled></Switch>
                <p>Switch </p>
                <br/><br/><br/><br/>
                <Switch defaultChecked></Switch>
                <p></p>
                <br/><br/><br/><br/>
                <Switch size={"small"}></Switch>
                <p>size="small" </p>
                <br/><br/><br/><br/>
                <Switch checkedChildren="1" unCheckedChildren="0"  />
                <br/><br/><br/><br/>
                <Switch checkedChildren="" unCheckedChildren=""  />
                <p></p>
                <br/><br/><br/><br/>
                <Switch checkedChildren={<i className="iconfont icon-check"></i>} unCheckedChildren={<i className="iconfont icon-close"></i>}  />
                <p></p>
            </div>
        );
    }
}

export default PageSwitch;